import React from 'react';
import './index.scss';
import _ from 'lodash';
import Select from 'react-select';
import Table from './table';
// import Data from '../../data/dashboard.json';
import Axios from 'axios';

const options = [];

let students = {};

class Dashboard extends React.Component {
  // constructor() {
  //   super();
  //   // this.mentors = Data.mentors;
  //   // const listOfMentors = _.sortBy(_.values(this.mentors), item => {
  //   //   return item.Nick;
  //   // });
  //   // this.tasks = Data.tasks || {};
  //   // _.forEach(listOfMentors, mentor => {
  //   //   options.push({
  //   //     value: mentor.Name,
  //   //     label: mentor.Nick,
  //   //   });
  //   // });
  // }

  state = {
    selectedOption: {},
    tableData: {},
    data: {},
    options: [],
  };

  handleChange = selectedOption => {
    students = this.mentors[selectedOption.label].Students;
    const tableData = {
      students: students,
      tasks: this.tasks,
    };
    this.setState({
      ...this.state,
      selectedOption,
      tableData,
    });
    const user = {
      selectedOption: selectedOption,
      tableData: tableData,
    };
    localStorage.setItem('mdLastUserState', JSON.stringify(user));
  };

  componentDidMount = () => {
    Axios('dashboard.json')
      .then(data => data.data)
      .then(e => {
        this.mentors = e.mentors;
        const listOfMentors = _.sortBy(_.values(this.mentors), item => {
          return item.Nick;
        });
        this.tasks = e.tasks || {};
        _.forEach(listOfMentors, mentor => {
          options.push({
            value: mentor.Name,
            label: mentor.Nick,
          });
        });
        this.setState({
          ...this.state,
          options: options,
        });
      });

    const user = JSON.parse(localStorage.getItem('mdLastUserState'));
    if (user) {
      const selectedOption = user.selectedOption;
      const tableData = user.tableData;
      const state = user.state;
      this.setState({
        ...this.state,
        selectedOption,
        tableData,
        state,
      });
    }
  };

  render() {
    const { selectedOption, tableData } = this.state;
    const name = selectedOption.value || '';
    return (
      <div className='Dashboard'>
        <h2>
          Mentor:{' '}
          <a
            href={`https://github.com/${selectedOption.label}`}
            target='_blank'
            rel='noopener noreferrer'
          >
            {name}
          </a>
        </h2>
        <Select
          className='ChooseMentor'
          value={selectedOption}
          onChange={this.handleChange}
          options={this.state.options}
        />
        <Table tableData={tableData} />
      </div>
    );
  }
}

export default Dashboard;
